@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



#root *:focus{
  outline: none;
}
#root{
  max-width: 1000px;
  margin: 30px auto;
  padding: 15px;
}
#root,p,a,input,select,textarea,button{
  font-family: 'Rubik', sans-serif;
}
#root input, textarea, select{
  padding: 5px;
}
#root label{
  margin: 5px;
}
label span{
  margin-right: 5px;
}
#calc-form-controlls-wrapper{
  margin: 30px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.detail-settings, .detail{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0 15px;
  margin: 10px 0;
  
}
.detail{
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-align-items: stretch;
          align-items: stretch;
  padding: 0;
  /* border: 5px double #25c3a6; */
}
.detail.active input[name=title]{
  font-size: 16px;
  color: #2a45c7;
  font-weight: 600;
}
.detail.active .detail-settings{
  background-color: #dde3e8;
  border-color: #3582c4;
}
.detail.active legend{
  background-color: #fff;
  border-radius: 7px;
  border-top: 1px solid #ddd;
}
.detail.not-active{
  height: 143px;
  border: 0;
}
.detail.not-active .detail-plan{
  padding: 0;
}
#root .remove-detail-btn{
  position: absolute;
  top: -1px;
  right: -1px;
  width: 40px;
  height: 40px;
  border-color: #d63638;
  color: #d63638;
  margin: 0;
  padding: 0;
}
.remove-detail-btn:after{
  content: '+';
  font-size: 40px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  line-height: 0;
  background-color: transparent;
}
.detail-plan{
  padding: 10px;
  background: #fff;
  width: 400px;
  max-height: 600px;
  border: 1px solid #ddd;
  position: relative;
  display: -webkit-flex;
  display: flex;
}
.detail-plan svg{
  max-height: 100%;
  max-width: 100%;
  margin: auto;
}
.detail-settings{
  background-color: #fafafa;
  margin: 0;
  padding: 0;
  border: 1px solid #ddd;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}
.detail-settings label span{
  font-size: 12px;
}
.detail-settings label span b{
  color: #3582c4;
  font-size: 14px;
}
#root .detail-ext-options{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10px;
  width: 120px;
  margin-left: auto;
}
#root .detail-ext-options button{
  font-size: 14px;
  margin: 5px;
}
#root .detail-ext-options button[data-action=rotate]{
  font-size: 11px;
}
.angle-options-wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  position: relative;
  padding: 5px;
  border: 1px solid #ddd;
  margin: 5px;
  min-height: 36px;
  width: calc(50% - 22px);
  display: -webkit-inline-flex;
  display: inline-flex;
}
.angle-options-wrapper.active{
  background-color: #ececec;
  width: calc(100% - 22px);
}
.angle-options-wrapper label{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
.angle-options__send{
  position: absolute;
  right: 0;
  top: 50px;
}
.angle-options-wrapper label:not(.switch){
  -webkit-align-items: center;
          align-items: center;
  width: 94%;
}
.angle-options-type{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
#root .angle-options-type button{
  background-color: #fff;
  border: 1px solid #3582c4;
  color: #3582c4;
  cursor: pointer;
  padding: 5px;
  font-size: 12px;
}
#root .angle-options-type button[data-active=active]{
  background-color: #3582c4;
  border: 1px solid #3582c4;
  color: #fff;
}
.angle-type__linear{
  margin: 0 5px;
}

.angle-options__tabs-header{
  margin-top: 20px;
}
.angle-options__tabs-header button.active{
  background-color: #3582c4;
  border: 1px solid #3582c4;
  color: #fff;
}
.angle-options__tabs-header button[disabled]{
  opacity: .5;
  cursor: default;
}
.angle-title-wrapper{
  margin: 0;
}
.detail-settings .angle-title{
  width: 100px;
  display: inline-block;
  font-size: 14px;
  padding-left: 50px;
  line-height: 1;
}
.angle-title::before{
  content: '';
  background-color: #eee;
  height: 48px;
  width: 48px;
  position: absolute;
  top: 0;
  left: 0;
}
.angle-title::after{
  content: '';
  border: 15px solid transparent;
  border-left: 15px solid #d63638;
  border-top: 15px solid #d63638;
  max-height: 0;
  max-width: 0;
  position: absolute;
  display: inline-block;
  top: 2px;
  left: 2px;
}
.angle-title.rt::after{
  border-left: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-right: 15px solid #d63638;
  border-top: 15px solid #d63638;
  top: 2px;
  left: 15px;
}
.angle-title.rb::after{
  border-left: 15px solid transparent;
  border-bottom: 15px solid #d63638;
  border-right: 15px solid #d63638;
  border-top: 15px solid transparent;
  top: 16px;
  left: 15px;
}
.angle-title.lb::after{
  border-left: 15px solid #d63638;
  border-bottom: 15px solid #d63638;
  border-right: 15px solid transparent;
  border-top: 15px solid transparent;
  top: 16px;
  left: 2px;
}

label input:not([type=checkbox]){
  width: calc(100% - 12px);
}
label>input[type=number]{
  max-width: 100px;
}
.switch{
  position: absolute;
  right: 0;
  top: 63px;
}

#root button{
  margin: 0 5px;
  padding: 7px 15px;
  font-size: 16px;
  background-color: #fff;
  border: 1px solid #3582c4;
  color: #3582c4;
  cursor: pointer;
  transition: all 0.3s;
}
#root button:disabled{
  opacity: 0.5;
  cursor: default;
}
#root button:not([disabled]):hover{
  box-shadow: 1px 3px 3px #999;
  background-color: #3582c4;
  color: #fff;
}
#root button.remove-detail-btn:hover{
  background-color: #d63638;
}
#root button.remove-detail-btn:hover:after{
  color: #fafafa;
}
#root input[type=range]{
  position: relative;
  margin: 0 35px;
  max-width: calc(100% - 70px);
}
#root input[type=range]:before{
  content: attr(min);
  position: absolute;
  left: -20px;
  top: 7px;
  font-size: 10px;
}
#root input[type=range]:after{
  content: attr(max);
  position: absolute;
  right: -20px;
  top: 7px;
  font-size: 10px;
}
#root input[type=checkbox]{
  width: 20px;
  height: 20px;
}
#root input.error{
  background-color: #d636397c;
  border: 1px solid;
}
#root fieldset{
  padding: 5px;
  margin: 7px;
  background-color: #fff;
}
#root legend{
  padding: 0 10px;
  font-size: 14px;
}
.fieldset-notice{
  font-size: 12px;
}

/* ============================= SUMMARY */
#summary{
  border: 3px double #2a3cc1;
  padding: 5px;
  font-size: 13px;
  color: #2a3cc1;
}
.summ-item{
  display: -webkit-inline-flex;
  display: inline-flex;
  padding: 3px 30px;
  width: calc(50% - 60px);
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.summ-item:first-child{
  width: calc(100% - 60px);
  font-size: 14px;
  color: brown;
  margin-bottom: 3px;
}
#summary p{
  text-align: center;
}

.fons12{
  font-size: 12px;
}

.w100p{width: 100%;}
.w50p{width: 50%;}

fieldset.w100p{
  width: calc(100% - 13px);
}
fieldset.w50p{
  width: calc(50% - 26px);
}

#root label.mt30{
  margin-top: 30px;
}
#root input[type=text]{
  max-width: 260px;
}
#root input,
#root select,
#root fieldset,
#root textarea{
  border: 1px solid #ddd;
}
#root legend{
  font-style: italic;
}
.ml-a{
  margin-left: auto!important;
}
.px10{padding: 0 10px;}
.pt10{padding-top: 5px;}

.flex{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.flex-nowrap{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
.flex-column{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.flex-center{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.action-link{
  border-bottom: 1px dotted;
  cursor: pointer;
}
#summary .action-link{
  margin: 10px 0;
}
.summ-item.more{
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
#summary table td{
  padding: 0px 5px;
}
#summary table tr:hover{
  background-color: #f3f3f3;
}

/* COLORS */
.black{color: #1d2327;}
.light-black, #root{color: #2c3338;}
.blue{color: #3582c4;}
.light-blue{color: #72aee6;}
.red{color: #d63638;}

.copyright{
  font-size: 12px;
  text-align: center;
  margin-top: 30px;
  color: #999;
}
.copyright a{
  color: #d63638;
}
